@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Courier", monospace;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    max-width: 678px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 290px;
    max-width: none;
    width: 90%;
  }
}

.button {
  padding: 24px 56px;
  border: 2px solid #ffffff;
  outline: 2px solid #ffffff;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
}

.arrow-button {
  border: none;
  background-color: rgba(255, 255, 255, 0);
  stroke: #afafaf;
  fill: none;
  cursor: pointer;
  transition: 0.3s ease;
}
.arrow-button:hover {
  stroke: #102654;
  stroke-width: 2px;
  transform: scale(120%);
}
.arrow-button:focus, .arrow-button:focus-within {
  stroke: #102654;
  outline: none;
  stroke-width: 2px;
}
.arrow-button:active {
  opacity: 0.7;
}

.arrow-button.swiper-button-disabled {
  stroke: #afafaf;
  stroke-width: 1px;
  opacity: 0.5;
  transform: none;
  cursor: text;
}

@media (max-width: 1199px) {
  .arrow-button svg {
    display: block;
    width: 16px;
    height: 32px;
  }
}
@media (max-width: 767px) {
  .arrow-button svg {
    width: 10px;
    height: 19px;
  }
}
.page {
  height: 100%;
}

.page__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main-header {
  position: absolute;
  width: 100%;
}

.main-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
}

.main-header__logo svg {
  fill: #ffffff;
}

.main-header__phone {
  position: relative;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  user-select: none;
  transition: 0.3s ease;
}
.main-header__phone::after {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background: #d10a42;
  height: 1px;
  transition: 0.3s ease;
}
.main-header__phone:hover::after {
  width: 100%;
  background: #ffffff;
}
.main-header__phone:focus, .main-header__phone:focus-within {
  outline: none;
}
.main-header__phone:focus::after, .main-header__phone:focus-within::after {
  width: 100%;
  background: #ffffff;
}
.main-header__phone:active {
  color: #b6bdcc;
}
.main-header__phone:active::after {
  background: #b6bdcc;
  width: 0;
}

@media (max-width: 1199px) {
  .main-header__wrapper {
    margin-top: 34px;
  }
  .main-header__logo {
    margin-top: 6px;
  }
  .main-header__logo svg {
    width: 82px;
    height: 17px;
  }
  .main-header__phone {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    margin-top: 15px;
  }
  .main-header__logo {
    margin-top: 0;
  }
  .main-header__logo svg {
    width: 69px;
    height: 24px;
  }
  .main-header__phone {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}
.main-container {
  margin: 0;
  padding: 0;
  flex-grow: 1;
}

.intro {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  min-height: 658px;
}

.intro__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 51px;
  padding-left: 19px;
  padding-right: 200px;
}

.intro__text-wrapper {
  position: relative;
  margin-top: 111px;
  display: flex;
  flex-direction: row-reverse;
  gap: 12px;
}

.intro__text-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.intro__text {
  margin: 0;
  padding: 0;
  margin-bottom: 14px;
  padding-right: 50px;
  align-self: end;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-transform: lowercase;
}

.intro__title {
  margin: 0;
  margin-bottom: 51px;
  max-width: 252px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  -webkit-text-stroke: 4px #ffffff;
  color: #ffffff;
  text-transform: uppercase;
}

.intro__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-top: 22px;
  padding: 0;
  list-style: none;
}

.intro__item {
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
