}

.intro__button {
  align-self: flex-end;
  padding: 21px 55px;
  padding-bottom: 18px;
  color: #ffffff;
  transition: 0.3s ease;
  user-select: none;
}
.intro__button:hover, .intro__button:focus, .intro__button:focus-within {
  background-color: #ffffff;
  color: #102654;
  box-shadow: 0 0 7px 0 #102654 inset, 0 0 2px 2px #ffffff;
}
.intro__button:active {
  background-color: #b6bdcc;
}

@media (max-width: 1199px) {
  .intro {
    background: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
    min-height: 554px;
  }
  .intro__wrapper {
    padding: 0;
    padding-top: 82px;
    gap: 30px;
  }
  .intro__wrapper img {
    width: 324px;
