    margin-top: 12px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 491px;
    background: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
  .intro__wrapper {
    justify-content: flex-end;
    padding-top: 108px;
    gap: 0;
  }
  .intro__wrapper img {
    display: none;
  }
  .intro__text-wrapper {
    width: 100%;
    margin-top: 0;
  }
  .intro__text-container {
    width: 100%;
  }
  .intro__text {
    margin-bottom: 14px;
    font-size: 28px;
    line-height: 34px;
  }
  .intro__title {
    margin-bottom: 24px;
  }
  .intro__button {
    width: 100%;
    margin-right: 0;
    padding-bottom: 20px;
    text-align: center;
  }
  .intro__list {
    position: absolute;
    left: 0;
    top: 113px;
  }
}
