    width: 100%;
    max-width: 100%;
  }
  .contacts__form-wrapper {
    padding-top: 64px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .contacts__form-title {
    margin-bottom: 33px;
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.03em;
  }
  .contacts__list-wrapper {
    padding-top: 63px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 61px;
    row-gap: 35px;
  }
  .contacts__list-title {
    font-size: 32px;
    line-height: 38px;
  }
  .contacts__lists-container {
    display: flex;
    flex-direction: column;
    row-gap: 17px;
  }
  .contacts__lists-container::after {
    content: none;
  }
}
.form {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

.form__input-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.form__field label {
  display: none;
}

.form__field input {
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-bottom: 1px solid #74819c;
  color: #ffffff;
  transition: 0.3s ease;
  padding-left: 0;
}
.form__field input:hover {
  background-color: #ffffff;
  color: #102654;
  border-color: transparent;
  box-shadow: 0 0 2px 2px #ffffff;
}
.form__field input:hover::placeholder {
  color: #74819c;
}
.form__field input:focus, .form__field input:focus-within {
  background-color: #ffffff;
  color: #102654;
  outline: none;
  border-color: transparent;
  box-shadow: 0 0 2px 2px #ffffff;
}
.form__field input:focus::placeholder, .form__field input:focus-within::placeholder {
  color: #74819c;
}

.form__field input::placeholder {
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}

.form button {
  padding-top: 16px;
  padding-bottom: 16px;
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  background-color: rgba(255, 255, 255, 0);
  color: #ffffff;
  user-select: none;
  cursor: pointer;
  transition: 0.3s ease;
}
.form button:hover, .form button:focus, .form button:focus-within {
  background-color: #ffffff;
  color: #102654;
  box-shadow: 0 0 7px 0 #102654 inset, 0 0 2px 2px #ffffff;
}
.form button:active {
  background-color: #b6bdcc;
}

@media (max-width: 1199px) {
  .form__input-wrapper {
    column-gap: 60px;
  }
}
@media (max-width: 767px) {
  .form__input-wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 31px;
    margin-bottom: 10px;
  }
}
.contact h4 {
  margin: 0;
  margin-bottom: 1px;
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: #102654;
}

.contact a,
.contact p {
  margin: 0;
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 23px;
  color: #102654;
}

.contact a {
  position: relative;
  padding-bottom: 2px;
}

.contact a::before {
  content: "";
  background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 0;
  height: 1px;
  transition: 0.3s ease;
}

.contact a:hover::before,
.contact a:focus::before {
  width: 100%;
}

.contact a:active {
  opacity: 0.7;
}

@media (max-width: 1199px) {
  .contact h4 {
    margin: 0;
    margin-bottom: 1px;
    font-family: "Roboto Condensed", "Courier", monospace;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: #102654;
  }
  .contact a,
.contact p {
    font-size: 18px;
    line-height: 33px;
  }
}
@media (max-width: 767px) {
  .contact p {
    line-height: 36px;
  }
}
.main-footer {
  background-color: #030a1a;
}

.main-footer__wrapper {
  display: flex;
  align-items: center;
  min-height: 108px;
}

.main-footer__nav {
  display: flex;
  align-items: center;
}

.main-footer__logo {
  margin-top: 7px;
  margin-right: 277px;
}

.main-footer__logo svg {
  vertical-align: middle;
  fill: #ffffff;
}

@media (max-width: 1199px) {
  .main-footer__wrapper {
    min-height: 114px;
  }
  .main-footer__logo {
    margin-top: -2px;
    margin-right: 62px;
  }
  .main-footer__logo svg {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    min-height: 260px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 33px;
    padding-top: 45px;
  }
  .main-footer__logo {
    margin-top: -2px;
    margin-right: 0;
    margin-bottom: 15px;
  }
  .main-footer__logo svg {
    width: 68px;
    height: 15px;
  }
}
.footer-nav__list {
  display: flex;
}
.footer-nav__list:last-child {
  margin-right: 229px;
}

.footer-nav__link {
  position: relative;
  display: inline-block;
  padding: 3px 0;
  margin-right: 40px;
  font-family: "Roboto Condensed", "Courier", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  user-select: none;
  transition: 0.3s ease;
}

.footer-nav__link::before {
  content: "";
  background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 0;
  height: 1px;
  transition: 0.3s ease;
}

.footer-nav__link:hover,
.footer-nav__link:focus {
  background-position: 0;
  outline: none;
}

.footer-nav__link:hover::before,
.footer-nav__link:focus::before {
  width: 100%;
}

.footer-nav__link:active,
.footer-nav__link:active::before {
  opacity: 0.7;
}

@media (max-width: 1199px) {
  .footer-nav__list:last-child {
    margin-right: 25px;
  }
  .footer-nav__link {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .footer-nav {
    display: grid;
    grid-template-areas: "logo second-column" "first-column second-column";
    column-gap: 46px;
  }
  .footer-nav__logo {
    grid-area: logo;
  }
  .footer-nav__list {
    flex-direction: column;
    row-gap: 15px;
  }
  .footer-nav__list:last-child {
    margin-right: 0;
  }
  .footer-nav__list--first-column {
    grid-area: first-column;
  }
  .footer-nav__list--second-column {
    grid-area: second-column;
  }
  .footer-nav__link {
    font-size: 16px;
    line-height: 19px;
  }
}
.socials {
  display: grid;
  grid-template-columns: repeat(3, 25px);
  grid-template-rows: 25px;
  gap: 11px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.socials__item {
  height: 25px;
  background: #ffffff;
  border-radius: 50%;
  transition: 0.3s ease;
}
.socials__item:hover {
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  transform: scale(125%);
}
.socials__item:focus, .socials__item:focus-within {
  outline: none;
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  transform: scale(125%);
}
.socials__item:active {
  opacity: 0.7;
}

.socials__link {
  position: relative;
  display: block;
  width: inherit;
  height: inherit;
}
.socials__link:focus, .socials__link:focus-within {
  outline: none;
}

.socials__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

